}
/* Gilroy-900 */
@font-face {
  font-display: swap;
  font-weight: 900;
  font-family: "Gilroy";
  font-style: normal;
  src: url("/fonts/Gilroy-Black.woff2") format("woff2"), url("/fonts/Gilroy-Black.woff") format("woff");
}
