  }
}
@media (min-width: 1180px) {
  h2 {
    font-size: 36px;
    line-height: 44px;
  }
}
@media (min-width: 1660px) {
  h2 {
    font-size: 50px;
    line-height: 62px;
  }
}

h3 {
  margin: 0;
  color: #000000;
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
}
@media (min-width: 700px) {
  h3 {
    font-size: 22px;
    line-height: 28px;
  }
}
@media (min-width: 1180px) {
  h3 {
    font-size: 18px;
    line-height: 22px;
  }
}
@media (min-width: 1660px) {
  h3 {
    font-size: 24px;
    line-height: 30px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.container {
  width: 340px;
  margin: 0 auto;
}
@media (min-width: 700px) {
  .container {
    width: 700px;
  }
}
@media (min-width: 1180px) {
  .container {
    width: 1180px;
  }
}
@media (min-width: 1660px) {
  .container {
    width: 1660px;
  }
}

/* Rating Stars */
.rating__stars {
  position: relative;
  display: block;
  width: 100%;
  height: 16px;
  font-size: 0;
}

.rating__stars::before {
  display: block;
  height: 100%;
  background-color: transparent;
  background-image: url(/icons/stars-empty.svg);
  background-repeat: no-repeat;
  background-position: 0 50%;
  content: "";
}

.rating__stars span {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  height: 100%;
  overflow: hidden;
}

.rating__stars span::before {
  display: block;
  height: 100%;
  background-color: transparent;
  background-image: url(/icons/stars-active.svg);
  background-repeat: no-repeat;
  background-position: 0 50%;
  content: "";
}

/* Rating Strenght */
.rating__strength {
  position: relative;
  display: block;
  width: auto;
  height: 12px;
  font-size: 0;
}

.rating__strength::before {
  display: block;
  height: 100%;
  background-color: transparent;
  background-image: url(/icons/coffee-strength-empty.svg);
  background-repeat: no-repeat;
  background-position: 0 50%;
  content: "";
}

.rating__strength span {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  height: 100%;
  overflow: hidden;
}

.rating__strength span::before {
  display: block;
  height: 100%;
  background-color: transparent;
  background-image: url(/icons/coffee-strength-active.svg);
  background-repeat: no-repeat;
  background-position: 0 50%;
  content: "";
}

/* Rating Feature */
.rating__feature {
  position: relative;
  display: block;
  width: auto;
  height: 6px;
  font-size: 0;
}

.rating__feature::before {
  display: block;
  height: 100%;
  background-color: transparent;
  background-image: url(/icons/feature-empty.svg);
  background-repeat: no-repeat;
  background-position: 0 50%;
  content: "";
}

.rating__feature span {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  height: 100%;
  overflow: hidden;
}

.rating__feature span::before {
  display: block;
  height: 100%;
  background-color: transparent;
  background-image: url(/icons/feature-active.svg);
  background-repeat: no-repeat;
  background-position: 0 50%;
  content: "";
}

/* Blocks */
