  background: url(/img/bg/spot-1.svg), url(/img/bg/coffee-bean-mobile-bg.png), url(/img/bg/spot-2.svg);
  background-repeat: no-repeat, no-repeat, no-repeat;
  background-position: calc(50% + 282px) 20px, calc(50% - 196px) 1176px, calc(50% - 300px) 984px;
  background-size: 478px 578px, 259px 259px, 440px 498px;
}
@media (min-width: 700px) {
  .page-main {
    background: url(/img/bg/spot-1.svg), url(/img/bg/coffee-bean-tablet-bg.png), url(/img/bg/spot-2.svg);
    background-repeat: no-repeat, no-repeat;
    background-position: calc(50% + 406px) 38px, calc(50% - 360px) 1752px, calc(50% - 625px) 1529px;
    background-size: 766px 932px, 531px 531px, 900px 1019px;
  }
}
@media (min-width: 1180px) {
  .page-main {
    background: url(/img/bg/spot-1.svg), url(/img/bg/coffee-bean-laptop-bg.png), url(/img/bg/spot-2.svg);
    background-repeat: no-repeat, no-repeat;
    background-position: calc(50% + 672px) -200px, calc(50% - 680px) 1112px, calc(50% - 780px) 700px;
    background-size: 1014px 1240px, 493px 493px, 825px 936px;
  }
}
@media (min-width: 1660px) {
  .page-main {
    background: url(/img/bg/spot-1.svg), url(/img/bg/coffee-bean-desktop-bg.png), url(/img/bg/spot-2.svg);
