.burger-button {
  position: relative;
  width: 29px;
  height: 16px;
  padding: 0;
  background: none;
  border: none;
  cursor: pointer;
}
.burger-button::before, .burger-button::after {
  position: absolute;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 2px;
  background-color: #000000;
  transition: all 0.3s;
  content: "";
}
.burger-button::before {
  top: 0;
  box-shadow: 0 7px 0 0 #000000;
}
@media (min-width: 700px) {
  .burger-button {
    width: 59px;
    height: 30px;
  }
  .burger-button::before, .burger-button::after {
    height: 4px;
  }
  .burger-button::before {
    box-shadow: 0 13px 0 0 #000000;
  }
}
.burger-button--close::before {
  top: 50%;
  box-shadow: none;
  transform: rotate(45deg);
}
.burger-button--close::after {
  top: 50%;
  transform: rotate(-45deg);
}

